import Vue from 'vue'
import VueRouter from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import SommeliresIndex from '@/components/sommeliers/sommeliresIndex'
import Shop from '@/components/shop/shopIndex'
import Cart from '@/components/shoppingCart/cartIndex'
import MyMain from '@/components/mine/myMain'

Vue.use(VueRouter)

export default new VueRouter({
  mode:"history",
  routes: [
    {
      path: '/',
      name: 'sommeliresIndex',
      component: SommeliresIndex
    },
    {
      path: '/shop',
      name: 'shop',
      component: Shop
    },
    {
      path: '/cart',
      name: 'cart',
      component: Cart
    },
    {
      path: '/mine',
      name: 'MyMain',
      component: MyMain
    }
  ]
})



